<template>
    <div>
        <el-form :inline="true" :model="conditions" class="demo-form-inline">   
            <el-form-item label="">
                <el-input v-model="conditions.case_name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.case_mobile" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.case_id_num" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.keywords" placeholder=""></el-input>
            </el-form-item>
            
             <el-form-item label="">
                <el-input v-model="conditions.case_code" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.case_status" placeholder="">
                    <el-option 
                        label="" 
                        value=""
                    ></el-option>
                    <el-option 
                        :label="item.label" 
                        :value="item.value"
                        v-for='item in caseStatusList'
                        :key="item.value"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.case_level" placeholder="">
                    <el-option 
                        label="" 
                        value=""
                    ></el-option>
                    <el-option 
                        v-for='item in caseLevelList'
                        :key='item.id'
                        :label='item.name'
                        :value='item.id'
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.client_id" filterable :filter-method='filterFn'  placeholder="">
                    <el-option
                        v-for="item in clientList"
                        :label="item.name" 
                        :value="item.id"
                        :key='item.id'>
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.min_case_money" type='number' placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="-">
                <el-input  v-model="conditions.max_case_money" type='number'  placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="conditions.min_talk_time" type='number' placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="-">
                <el-input  v-model="conditions.max_talk_time" type='number' placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                    v-model="conditions.min_case_date"
                    type="date"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="-">
                <el-date-picker
                    value-format="yyyy-MM-dd HH:mm:ss"
                    v-model="conditions.max_case_date"
                    type="date"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                    value-format="yyyy-MM-dd HH:mm:ss"
                    v-model="conditions.min_case_back_date"
                    type="date"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="-">
                <el-date-picker
                    value-format="yyyy-MM-dd HH:mm:ss"
                    v-model="conditions.max_case_back_date"
                    type="date"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="">
                 <el-select v-model="conditions.depart_id" @change="changeFn" placeholder="">
                    <el-option 
                        label="" 
                        value=""
                    ></el-option>
                    <el-option
                        v-for="item in departmentList"
                        :class="item.class_name"
                        :key="item.id"
                        :label="item.depart_name"
                        :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.staff_id" placeholder="">
                    <el-option 
                        label="" 
                        value=""
                    ></el-option>
                    <el-option
                        v-for="item in staffList"
                        :key="item.id"
                        :label="item.true_name"
                        :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.batch_id" placeholder="">
                    <el-option 
                        label="" 
                        value=""
                    ></el-option>
                    <el-option 
                        v-for="item in batchList"
                        :key='item.id'
                        :label="item.batch_name" 
                        :value="item.id"
                    ></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="conditions.case_color" placeholder="">
                    <el-option label="" value="1"></el-option>
                    <el-option label="" value="2"></el-option>
                    <el-option label="" value="3"></el-option>
                    <el-option label="" value="4"></el-option>
                    <el-option label="" value="5"></el-option>
                    <el-option label="" value="6"></el-option>
                    <el-option label="" value="7"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
                <el-date-picker
                    value-format="yyyy-MM-dd HH:mm:ss"
                    v-model="conditions.min_case_last_collection_date"
                    type="date"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="-">
                <el-date-picker
                    value-format="yyyy-MM-dd HH:mm:ss"
                    v-model="conditions.max_case_last_collection_date"
                    type="date"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
import '@/styles/css/other.css'
export default {
    name:'formCaseFirst',
    props:[
        'conditions',
        'caseLevelList',
        'batchList',
        'departmentList',
        'staffList',
        'clientList',
        'caseStatusList',
    ],
    methods:{
        filterFn(val){
            this.$emit('filterFn',val)
        },
        changeFn(val){
            this.$emit('changeFn',val)
        }
    }
}
</script>





    


