<template>
    <div>
        <div class="info">
            <h3></h3>
            <div class="redS"><span>{{selfInfo.case_code}}</span><span><el-button type="primary" @click="totalCaseFn('total')" size="mini">{{selfInfo.case_total}}</el-button></span><span>{{selfInfo.case_level}}</span><span><el-button type="primary" v-show="selfInfo.case_status!='3'" @click="changeLevelFn" size="mini"></el-button></span></div>
            <div><span>{{selfInfo.case_id_num}}  </span><span>{{selfInfo.case_mobile}}</span><span><el-button  type="primary" v-show="selfInfo.case_status!='3'" @click="callFn(selfInfo.case_mobile,'')" size="mini"></el-button></span><span>{{selfInfo.case_home_address}}  </span><span><el-button   v-show="selfInfo.case_status!='3'" type="success" @click="sendLetter(2,selfInfo.case_mobile,'')" size="mini"></el-button></span><span><el-button  v-show="selfInfo.case_status!='3'"  type="danger" @click="sendLetter(3,selfInfo.case_mobile,'')" size="mini"></el-button></span></div>
            <div><span>{{selfInfo.case_organization_name}}  </span><span>{{selfInfo.case_work_phone}}</span><span><el-button  type="primary" v-show="selfInfo.case_status!='3'"  @click="callFn(selfInfo.case_work_phone,'')" size="mini"></el-button></span><span>{{selfInfo.case_work_address}}  </span><span><el-button  type="success" v-show="selfInfo.case_status!='3'"  @click="sendLetter(2,selfInfo.case_work_phone,'')" size="mini"></el-button></span><span><el-button   v-show="selfInfo.case_status!='3'"  type="danger" @click="sendLetter(3,selfInfo.case_work_phone,'')" size="mini"></el-button></span></div>
            <div><span>{{selfInfo.case_mobile1}}  </span><span><el-button  type="primary" v-show="selfInfo.case_status!='3'"  @click="callFn(selfInfo.case_mobile1,'')" size="mini"></el-button></span><span>{{selfInfo.case_mobile2}}  </span><span><el-button  v-show="selfInfo.case_status!='3'" type="primary" @click="callFn(selfInfo.case_mobile2,'')" size="mini"></el-button></span></div>
        </div>
        <div class="info">
            <h3></h3>
            <div><span>{{selfInfo.case_money}}  </span><span>{{selfInfo.case_paid}}  </span><span>{{selfInfo.client}}  </span><span>{{selfInfo.case_exceed_limit}}  </span><span>{{selfInfo.case_interest}}  </span><span>{{selfInfo.case_back_date}}  </span></div>
        </div>
    </div>
    
</template>
<script>
export default {
    name:'divInfo',
    props:[
        'selfInfo'
    ],
    methods:{
        totalCaseFn(str){
            this.$emit('totalCaseFn',str)
        },
        changeLevelFn(){
            this.$emit('changeLevelFn')
        },
        callFn(val,num){
            this.$emit('callFn',val,num)
        },
        sendLetter(val1,val2,val3){
            this.$emit('sendLetter',val1,val2,val3)
        }
    }
}
</script>




